<template>
	<transition name="fade">
		<view v-show="window.scrollY >= scrollY" @click="goTop" :style="{
        width: `${width}px`,
        height: `${height}px`,
        right: `${right}px`,
        bottom: `${bottom}px`,
        zIndex: zIndex
      }"
		 class="gotop">
			{{wscrollY}}
		</view>
		</view>
	</transition>
</template>

<script>
	export default {
		name: 'ourGoTop',
		data() {
			return {
				timer: null,
				isShow: false
			}
		},
		props: {
			width: {
				type: Number,
				default: 50
			},
			height: {
				type: Number,
				default: 50
			},

			right: {
				type: Number,
				default: 30
			},
			bottom: {
				type: Number,
				default: 50
			},
			zIndex: {
				type: Number,
				default: 1000
			},
			color: {
				type: String,
				default: '#333'
			},
			scrollY: {
				type: Number,
				default: 1000
			},
			scrollSpeed: {
				type: Number,
				default: 100
			},
			duration: {
				type: Number,
				default: 300
			}
		},
		mounted() {
			//#ifndef H5
			this.isShow = true
			//#endif

			//#ifdef  H5
			window.addEventListener('scroll', this.scrollAnimate)
			//#endif
		},
		beforeDestroy() {
			//#ifdef H5
			window.removeEventListener('scroll', this.scrollAnimate)
			//#endif
		},
		methods: {
			//#ifndef H5
			goTop() {
				uni.pageScrollTo({
					scrollTop: 0,
					duration: this.duration
				});
			}
			//#endif

			//#ifdef H5
			scrollAnimate() {
				if (window.scrollY >= this.scrollY && this.isShow === false) {
					this.isShow = true
				} else if (window.scrollY < this.scrollY && this.isShow === true) {
					this.isShow = false
				}
			},
			goTop() {
				let self = this
				if (self.timer) {
					cancelAnimationFrame(self.timer);
				}
				self.timer = requestAnimationFrame(function fn() {
					let oTop = document.body.scrollTop || document.documentElement.scrollTop;
					if (oTop > 0) {
						document.body.scrollTop = document.documentElement.scrollTop = oTop - self.scrollSpeed;
						self.timer = requestAnimationFrame(fn);
					} else {
						cancelAnimationFrame(self.timer);
					}
				})
			}
			//#endif
		}
	}
</script>

<style scoped>
	.gotop {
		position: fixed;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		box-shadow: 0 5rpx 15rpx 0 rgba(0, 0, 0, .2);
	}

	.fade-enter,
	.fade-leave-to {
		opacity: 0;
	}

	.fade-enter-to,
	.fade-leave {
		opacity: 1;
	}

	.fade-enter-active,
	.fade-leave-active {
		transition: 0.5s all linear
	}
</style>
